import React, { useState } from "react";
import { CoinSwapPairABI, erc20FactoryABI, autoERC20ABI } from "./abi/abi";
import { ethers, BigNumber } from 'ethers';
import "./App.css";
import { Contract } from '@ethersproject/contracts';
 
//method 1:
//import { getDefaultProvider } from '@ethersproject/providers';
//import { getNetwork } from '@ethersproject/networks';
 
//method 3:
//import { createWatcher } from '@makerdao/multicall';

const erc20FactoryAddress = "0xe03163cF98465662AB9447180da05e3390d700F8";

//method 1:
//const provider = getDefaultProvider(getNetwork(3));
//const provider = new ethers.providers.Web3Provider(window.ethereum);
//const signer = ethers.Wallet.createRandom();
//const signer = provider.getSigner();
//const CoinSwapPairContract = new Contract(cswpPairAddress, CoinSwapPairABI, provider)
//const erc20FactoryContract = new Contract(erc20FactoryAddress, erc20FactoryABI, provider)

//method 2:
const provider = new ethers.providers.Web3Provider(window.ethereum);
const signer = provider.getSigner();
const erc20FactoryContract = new Contract(erc20FactoryAddress, erc20FactoryABI, signer)
//let provider = new ethers.providers.Web3Provider(web3.currentProvider);
//let infuraProvider = new ethers.providers.InfuraProvider('ropsten');


function App() {
  const [name, setName] = useState("_");
  const [symbol, setSymbol] = useState("_");
  const [tokenAddress, setTokenAddress] = useState("0x...");
  const [recipientAddress, setRecipient] = useState("0x...");
  const [transferAmount, setAmount] = useState(0);
  const [pairAddress, setPairAddress] = useState("0x1ceE98B938F0AF112592353F0353AFEdE50d8eEA");
  const [reserve0, setReserve0] = useState("_");
  const [reserve1, setReserve1] = useState("_");
  const [ico, setICO] = useState("_");
  const [scalar0, setScalar0] = useState("_");
  const [scalar1, setScalar1] = useState("_");
  const [r, setR] = useState("_");
  const [lambda0, setLambda0] = useState("_");
  const [lambda1, setLambda1] = useState("_");
  const [mu, setMu] = useState("_");
  const [token0, setToken0] = useState("_");
  const [token1, setToken1] = useState("_");


  const getData = async (t) => {
    t.preventDefault();
    const _2=BigNumber.from(2)
    const CoinSwapPairContract = new ethers.Contract(pairAddress, CoinSwapPairABI, signer);
    const [reserve, circle] = await CoinSwapPairContract.getReserves();
    const circleData = BigNumber.from(circle.toString())
    const reserves = BigNumber.from(reserve.toString())

    const reserve0 = reserves.div(_2.pow(BigNumber.from(128)))
    const reserve1 = (reserves.div(_2.pow(BigNumber.from(32)))).mod(_2.pow(BigNumber.from(96)))
    //circleData = ICO(8) | 10**(18-d0) (56) | 10**(18-d1) (56)| r (16)| lambda0(16)| lambda1(16)| mu(56)
    const ICO = circleData.div(_2.pow(BigNumber.from(216)))
    const scalar0 =(circleData.div(_2.pow(BigNumber.from(160)))).mod(_2.pow(BigNumber.from(56)))
    const scalar1 =(circleData.div(_2.pow(BigNumber.from(104)))).mod(_2.pow(BigNumber.from(56)))
    const r =(circleData.div(_2.pow(BigNumber.from(88)))).mod(_2.pow(BigNumber.from(16)))
    const lambda0 =(circleData.div(_2.pow(BigNumber.from(72)))).mod(_2.pow(BigNumber.from(16)))
    const lambda1 =(circleData.div(_2.pow(BigNumber.from(56)))).mod(_2.pow(BigNumber.from(16)))
    const mu =circleData.mod(_2.pow(BigNumber.from(56)))
    setReserve0(reserve0.toString())
    setReserve1(reserve1.toString())
    setICO(ICO.toString())
    setScalar0(scalar0.toString())
    setScalar1(scalar1.toString())
    setR(r.toString())
    setLambda0(lambda0.toString())
    setLambda1(lambda1.toString())
    setMu(mu.toString())
    const pairtoken0 = await CoinSwapPairContract.token0();
    const pairtoken1 = await CoinSwapPairContract.token1();
    setToken0(pairtoken0.toString())
    setToken1(pairtoken1.toString())
  };
  
  const deployERC = async (t) => {
    t.preventDefault();
    //const accounts = await window.ethereum.enable();
    //const account = accounts[0];
    //const gas = await erc20FactoryContract.estimateGas.createERC20(name, symbol);
    await erc20FactoryContract.createERC20(name, symbol);
  };
  

  const transferERC = async (t) => {
    t.preventDefault();
    //const accounts = await window.ethereum.enable();
    //const account = accounts[0];
    const erc20TokenContract = new Contract(tokenAddress, autoERC20ABI, signer)
    await erc20TokenContract.transfer(recipientAddress, transferAmount);//, {gasLimit: 1000000});
  };


 return (
  <div className="cargo">
    <title>Deploy ERC20 tokens </title>
    <div className="case">

    <h1>Deploy an ERC20 token</h1>
      <form className="form" onSubmit={deployERC}>
        <label>
          <input
            className="input"
            type="text"
            name="name"
            placeholder="name (e.g., my token)"
            onChange={(t) => setName(t.target.value)}
          />
          <input
            className="input"
            type="text"
            name="symbol"
            placeholder="symbol (e.g., USD)"
            onChange={(t) => setSymbol(t.target.value)}
          />
          <br></br>Name: {name} 
          <br></br>Symbol: {symbol}
        </label>
        <button className="button" type="submit" value="Confirm">
          Deploy ERC20 token {symbol}
        </button>
      </form>
      <h1>Transfer ERC20 tokens</h1>
      <form className="form" onSubmit={transferERC}>
        <label>
          <input
            className="input"
            type="text"
            name="ercToken"
            placeholder="Tokens Address (0x...)"
            onChange={(t) => setTokenAddress(t.target.value)}
          />
          <input
            className="input"
            type="text"
            name="recipient"
            placeholder="Recipient(0x...)"
            onChange={(t) => setRecipient(t.target.value)}
          />
          <input
            className="input"
            type="number"
            name="recipient"
            placeholder="transfer Amount"
            onChange={(t) => setAmount(t.target.value)}
          />
          <br></br>Token: {tokenAddress}
          <br></br>Recipient: {recipientAddress}
          <br></br>Amount: {transferAmount}
        </label>
        <button className="button" type="submit" value="Confirm">
          Transfer {transferAmount}
        </button>
      </form>

      <h1>Retrieve Pair Reserve/Circle</h1>
      <form className="formhigh" onSubmit={getData}>
        <label>
          <input
            className="input"
            type="text"
            name="pair"
            placeholder="Pair Address (0x...)"
            onChange={(t) => setPairAddress(t.target.value)}
          />
          <br></br>Pair: {pairAddress}
          <br></br>reserve0: {reserve0}
          <br></br>reserve1: {reserve1}
          <br></br>ICO: {ico} &nbsp;&nbsp;&nbsp; scalar0: {scalar0} &nbsp;&nbsp;&nbsp; scalar1: {scalar1}
          &nbsp;&nbsp;&nbsp; r: {r}
          <br></br>lambda0: {lambda0} lambda1: {lambda1}
          <br></br>mu: {mu}
          <br></br>token0: {token0}
          <br></br>token1: {token1}
        </label>
        <button className="button" type="submit" value="Confirm">
          Retrieve {pairAddress}
        </button>
      </form>
      
  </div>
      </div>



);
}

export default App;

